/* -----------------------------------------------------------------------------
  Root (inc)
----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: 77px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

#icon-sprite {
  display: none;
}

.inner-body {
  position: relative;
  width: 100%;
}

main {
  padding-left: 30px;
  padding-right: 15px;
  min-height: calc(100vh - 77px - 60px - 70px);
}

main img {
  max-width: 100%;
  margin-bottom: 1rem;
}

main p {
  font-family: Manrope, sans-serif;
  font-style: normal;
  letter-spacing: 0.03em;
}

main table {
  margin-bottom: 1rem;
}

main table td,
main table th {
  vertical-align: baseline;
}

.sidebar {
  flex-shrink: 0;
}

.sidebar .sidebar-container {
  top: 0;
  overflow-y: auto;
}

a {
  color: #256bd1;
}

a:hover, a:focus {
  color: #3585f9;
}

a:active {
  color: #256bd1;
  font-weight: 700;
}

th p {
  margin: 0;
  font-weight: inherit;
  font-weight: 500;
}

/* -----------------------------------------------------------------------------
  Component: Mode switch

  Style for the component that switches the theme style to light or dark mmode.
----------------------------------------------------------------------------- */
#light-dark-switch {
  display: flex;
  flex-direction: row;
  padding-bottom: 8px;
}

#light-dark-switch > span:first-child {
  margin-left: 8px;
}

#light-dark-switch .switch-icon {
  width: 20px;
  height: 20px;
  margin-left: 5px;
  margin-right: 5px;
  display: inline-block;
  background-size: contain;
  background-position: center;
}

#light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun");
}

#light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon");
}

#mode-switch {
  box-sizing: content-box;
  position: relative;
  top: 1px;
  display: flex;
  flex-direction: row;
  width: 26px;
  height: 16px;
  padding: 0;
  margin: 0 5px;
  background-color: #3585f9;
  border: 1px solid #3585f9;
  border-radius: 25px;
}

#mode-switch.dark {
  justify-content: flex-end;
}

#mode-switch.dark span {
  left: calc(100% - 16px);
}

#mode-switch span {
  position: absolute;
  left: 0;
  display: inline-block;
  width: 16px;
  height: 16px;
  content: "";
  background-color: #fff;
  border-radius: 50%;
  transition: left ease-in .2s;
}

/* -----------------------------------------------------------------------------
  Component: Search box

  Style for the searchbox in general.
----------------------------------------------------------------------------- */
.search-box {
  background-color: #ebf2fb;
  border: 2px solid #c1ccd9;
  border-radius: 10px;
}

.search-box .search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 46px;
}

.search-box .search-form .form-control {
  position: relative;
  z-index: 1;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  background-color: #ebf2fb;
  border: none;
}

.search-box .search-form .btn-search {
  position: absolute;
  right: 0;
  z-index: 10;
  width: 46px;
  height: 46px;
  padding: 5px;
  background: url("../../images/icons/svg-icons.svg#search");
  background-size: 41px 41px;
  background-position: center;
  border: none;
}

